.search-app .search-app-wrapper {
  padding: 1.5rem 0;
}
.search-app .search-app-wrapper-active {
  padding: 1.5rem 0 0;
}
.search-app .search-app-list-element {
  display: block;
  padding: 0;
  margin: 0;
}
.search-app .search-app-list-element h3 {
  padding: 0;
  margin: 0;
  font-size: 1rem;
}
.search-app .search-app-list-element .search-app-title {
  border: none;
  line-height: 1rem;
  padding-top: 8px;
}
.search-app .search-app-list-element .search-app-img-link {
  display: block;
  float: left;
  margin-right: 10px;
  margin-bottom: 0;
}
.search-app .search-app-list-element .search-app-url {
  color: #888;
}
.search-app .search-app-list-element .search-app-date {
  color: #7b8b9d;
}
.search-app .list-element-divider {
  border-bottom: 1px solid #687686;
  margin: 0 2px;
}


